import React, {Component} from 'react';
import Detail from './Detail';
import axios from "axios";
import uuid from 'uuid';

class Details extends Component {
	state = {
		weathers: [],
		title: null,
		loading: true
	};
	
	async componentDidMount() {
		const woeId = this.props.location.pathname.split('/weather/')[1];
		const res = await axios.get(`https://www.metaweather.com/api/location/${woeId}`);
		
		this.setState({weathers: res.data.consolidated_weather});
		this.setState({title: res.data.title, loading: false});
	}
	render() {
		const {title, weathers, loading} = this.state;
		if (loading === true) {
			return (
				<div className="card mb-3">
					<div className="card-body">
						<div className="lds-ring">
							<div></div>
							<div></div>
							<div></div>
							<div></div>
						</div>
					</div>
				</div>
			)
		}
		
		return (
			<div className="card mb-3">
				<div className="card-header">
					<h2>{title}</h2>
				</div>
				<div className="card-body">
					{weathers.map(w =>
						<Detail key={uuid()} the_temp={w.the_temp} max_temp={w.max_temp} min_temp={w.min_temp} applicable_date={w.applicable_date}
							weather_state_abbr={w.weather_state_abbr} humidity={w.humidity}/>
					)}
				</div>
			</div>
		);
	}
}

export default Details;