import React from 'react'

function Detail(props) {
	return (
		<React.Fragment>
			<ul>
				<li>
					<b>{new Date(props.applicable_date).toDateString()}</b>
					<img className="float-right" src={`https://www.metaweather.com/static/img/weather/${props.weather_state_abbr}.svg`} alt="" height="25px"/>
					<ul>
						<li>
							Temperature: {parseInt(props.the_temp)}°c
						</li>
						<li>
							Maximum Temperature: {parseInt(props.max_temp)}°c
						</li>
						<li>
							Minimum Temperature: {parseInt(props.min_temp)}°c
						</li>
						<li>
							Humidity: {props.humidity}%
						</li>
					</ul>
				</li>
			</ul>
		</React.Fragment>
	);
}

export default Detail;